* {
	margin: 0;
	padding: 0;
}

html, body {
	width: 100%;
	height: 100%;
}

div.splash, div.wait, div#root {
	width: 100%;
	height: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
}


div.splash {
	width: 100%;
	height: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	background: -webkit-linear-gradient(171deg, #ff5353, #f7ff53, #53ff93, #53c4ff);
	background: -o-linear-gradient(171deg, #ff5353, #f7ff53, #53ff93, #53c4ff);
	background: linear-gradient(279deg, #ff5353, #f7ff53, #53ff93, #53c4ff);
	background-size: 1600% 1600%;
	-webkit-animation: AnimationName 3s ease infinite;
	animation: AnimationName 3s ease infinite;
}

div.splash img.logo {
	width: 90%;
}

@-webkit-keyframes AnimationName {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}
@keyframes AnimationName { 
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}

div.wait {
	-ms-flex-direction: column;
	    flex-direction: column;
	background: #161616;
	color: #fff;
	font-family: -apple-system, BlinkMacSystemFont, "Helvetica Neue", "Noto Sans Japanese", "ヒラギノ角ゴ ProN W3", Hiragino Kaku Gothic ProN, Arial, Meiryo, sans-serif;
}

div.wait img.logo {
	width: 90%;
}

div.wait h3 {
	margin: 40px 0 20px;
	width: 85%;
	text-align: center;
	color: #A6FF3E;
	font-size: 4vw;
	line-height: 8vw;
	border: solid 2px #A6FF3E;
}

div.wait p {
	margin: 10px;
	width: 85%;
	text-align: center;
}

div.drawColor {
	width: 100vw;
	height: 100vh;
}

@-webkit-keyframes AnimationName {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}
@keyframes AnimationName { 
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}


